import {
	addTrainingApply,
	getTrainingApply,
	getTrainingDetail,
} from '@/api/job'
import {
	$wuxToast
} from '../../wxcomponents/wux/index'
import QQMapWX from '../../common/qqmap-wx-jssdk.min.js';
var qqmapsdk;
export default {
	data() {
		return {
			apply: false,
			detail: {
				type: 1,
				pubdate: 1571801261,
			},
			open: true,
		}
	},
	onShow() {
		if (this.$store.getters.register && this.detail.id) {
			this.getTrainingApply()
		}
		this.open = true
	},
	onLoad(e) {
		this.getTrainingDetail(e.id)
		qqmapsdk = new QQMapWX({
			key: 'I3ABZ-QPZW3-JIP3T-3AVMR-TRALT-77BFQ'
		});
	},
	methods: {
		getTrainingDetail(id) {
			getTrainingDetail({
				id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
					this.detail.info.tag = r.detail.info.tag.split(',')
					if (this.$store.getters.register && this.detail.id) {
						this.getTrainingApply()
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		getTrainingApply() {
			getTrainingApply({
				id: this.detail.id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.apply = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		addTrainingApply(e) {
			if (this.$store.getters.register) {
				if (this.$store.getters.member.ac > 1) {
					addTrainingApply({
						id: this.detail.id
					}).then(response => {
						let r = response.data
						if (response.code === 20000 && r.add) {
							this.apply = true
							$wuxToast().show({
								type: 'success',
								text: '报名成功'
							})
						} else if (response.code === 20000 && r.detail) {
							this.apply = true
							$wuxToast().show({
								type: 'success',
								text: '已报名'
							})
						} else {
							$wuxToast().show({
								type: 'forbidden',
								text: '请求异常'
							})
						}
					})
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '账号未认证'
					})
					setTimeout(() => {
						uni.navigateTo({
							url: '/pages/my/ac'
						});
					}, 1000)
				}
			} else {






				uni.navigateTo({
					url: '/pages/login/wx'
				});

			}
		},
		openMap() {
			let o = this.open
			if (o) {
				o = false
				const d = this.detail
				qqmapsdk.geocoder({
					address: d.province + d.city + d.area + d.info.address,
					success: res => {
						const location = res.result.location
						uni.openLocation({
							latitude: location.lat,
							longitude: location.lng,
							success: function() {
								setTimeout(() => {
									o = true
								}, 2000)
							},
							fail: function() {
								o = true
							},
						});
					}
				});
			}
		}
	}
}
